const handleSubmitCopyScheme = async () => {
  if (!currentSchemeForEdit.value) return
  
  try {
    const newScheme = await routeApi.createRouteScheme({
      id: copySchemeForm.id,
      description: copySchemeForm.description,
      enabled: false
    })
    
    const rules = await routeApi.getRules(currentSchemeForEdit.value.id)
    if (rules && rules.length > 0) {
      for (const rule of rules) {
        await routeApi.addRule(newScheme.id, {
          name: rule.name,
          type: rule.type,
          pattern: rule.pattern,
          priority: rule.priority || 0,
          outbounds: rule.outbounds,
          enabled: rule.enabled || true
        })
      }
    }
    
    ElMessage.success('复制方案成功')
    copySchemeDialogVisible.value = false
    await fetchRouteSchemes()
