const handleMoveRule = async (rule: RouteRule, direction: 'up' | 'down') => {
  if (!currentScheme.value) return
  
  const rules = currentScheme.value.rules
  const index = rules.findIndex(r => r.id === rule.id)
  
  if (direction === 'up' && index > 0) {
    [rules[index], rules[index - 1]] = [rules[index - 1], rules[index]]
  } else if (direction === 'down' && index < rules.length - 1) {
    [rules[index], rules[index + 1]] = [rules[index + 1], rules[index]]
  }
  
  try {
    const ruleIds = rules.map(r => r.id)
    await routeApi.updateRuleOrder(currentScheme.value.id, ruleIds)
    ElMessage.success('规则顺序更新成功')
    await fetchRouteSchemes()
    currentScheme.value = routeSchemes.value.find(s => s.id === currentScheme.value?.id) || null
  } catch (error) {
    console.error('更新规则顺序失败', error)
    ElMessage.error('更新规则顺序失败')
  }
}

