// 在 script setup 部分添加处理 Tab 键的方法
const handleTabKey = (e: KeyboardEvent) => {
  const textarea = e.target as HTMLTextAreaElement
  const start = textarea.selectionStart
  const end = textarea.selectionEnd
  
  proxyForm.config = proxyForm.config.substring(0, start) + '    ' + proxyForm.config.substring(end)
  
  // 将光标移动到插入的制表符后面
  nextTick(() => {
    textarea.selectionStart = textarea.selectionEnd = start + 4
  })
}

const handleShiftTabKey = (e: KeyboardEvent) => {
  const textarea = e.target as HTMLTextAreaElement
  const start = textarea.selectionStart
  const end = textarea.selectionEnd
  
  // 获取当前行的开始位置
  const beforeCursor = proxyForm.config.substring(0, start)
  const lines = beforeCursor.split('\n')
  const currentLineStart = start - lines[lines.length - 1].length
  
  // 检查当前行前面是否有4个空格
  const beforeSpaces = proxyForm.config.substring(currentLineStart, start).match(/^    /)
  if (beforeSpaces) {
    proxyForm.config = proxyForm.config.substring(0, currentLineStart) + proxyForm.config.substring(start)
    
    // 将光标移动到删除空格后的位置
    nextTick(() => {
      textarea.selectionStart = textarea.selectionEnd = currentLineStart
    })
  }
}

