<template>
  <el-card  shadow="never" class="network-graph">
    <template #header>
      <div class="card-header">
        <span>路由拓扑图</span>
        <el-button type="primary" @click="refreshGraph">刷新</el-button>
      </div>
    </template>
    <v-chart class="chart" :option="option" autoresize />
  </el-card>
</template>

<script setup lang="ts">
import { ref, onMounted, watch, nextTick, onUnmounted } from 'vue'
import { use } from 'echarts/core'
import { CanvasRenderer } from 'echarts/renderers'
import { GraphChart } from 'echarts/charts' 
import type { ProxyBase as Proxy, UserGroup, RouteScheme } from '../api/apiTypes'
import {
  TitleComponent,
  TooltipComponent,
  LegendComponent
} from 'echarts/components'
import VChart from 'vue-echarts'

use([
  CanvasRenderer,
  GraphChart,
  TitleComponent,
  TooltipComponent,
  LegendComponent
])
 

interface GraphNode {
  id: string
  name: string
  symbolSize: number | [number, number]
  symbol?: string
  path?: (string | number)[][]
  category: number
  value?: number
  itemStyle: {
    color: string
  }
  x?: number
  y?: number
  fixed?: boolean
}

interface GraphLink {
  source: string
  target: string
  info?: string
  value: number
}

const props = defineProps<{
  inboundProxies: Proxy[]
  outboundProxies: Proxy[]
  userGroups: UserGroup[]
  routeSchemes: RouteScheme[]
}>()

const option = ref<ECOption>({
  title: { 
    top: 0,
    left: 'center'
  },
  tooltip: {
    trigger: 'item',
    formatter: (params: any) => {
      if (params.dataType === 'node') {
        return `${params.data.name}`
      } else if (params.dataType === 'edge') {
        if (params.data.info) {
          return `${params.data.info}`
        } else {
          return `${params.data.source} -> ${params.data.target}`
        }
      }
    }
  },
  legend: {
    data: [
      {
        name: '入站代理',
        icon: 'circle',
        itemStyle: {
          color: '#4CAF50'
        }
      },
      {
        name: '用户组',
        icon: 'circle',
        itemStyle: {
          color: '#2196F3'
        }
      },
      {
        name: '路由方案',
        icon: 'circle',
        itemStyle: {
          color: '#FF9800'
        }
      },
      {
        name: '出站代理',
        icon: 'circle',
        itemStyle: {
          color: '#9C27B0'
        }
      }
    ],  
  },
  animationDurationUpdate: 150,
  animationEasingUpdate: 'quinticInOut' as const,
  series: [
    {
      type: 'graph',
      layout: 'none',
      data: [] as GraphNode[],
      links: [] as GraphLink[], 
      categories: [
        { name: '入站代理', itemStyle: { color: '#4CAF50' } },
        { name: '用户组', itemStyle: { color: '#2196F3' } },
        { name: '路由方案', itemStyle: { color: '#FF9800' } },
        { name: '出站代理', itemStyle: { color: '#9C27B0' } }
      ],
      label: {
        show: true,
        position: 'bottom',
        formatter: '{b}',
        backgroundColor: 'transparent',
        borderWidth: 0,
        color: '#333',
        fontSize: 12,
        distance: 10
      },
      lineStyle: {
        color: '#999',
        width: 2,
        curveness: 0.1
      },
      emphasis: {
        focus: 'adjacency',
        lineStyle: {
          width: 4
        }
      }, 
    }
  ]
})

// 监听数据变化
watch(
  [() => props.inboundProxies, () => props.outboundProxies, () => props.userGroups, () => props.routeSchemes],
  () => {
    nextTick(() => {
      generateGraphData()
    })
  },
  { deep: true }
)

const generateGraphData = () => {
  console.log('Generating graph data with:', {
    inboundProxies: props.inboundProxies,
    userGroups: props.userGroups,
    routeSchemes: props.routeSchemes,
    outboundProxies: props.outboundProxies
  })
  

  const nodes: GraphNode[] = []
  const links: GraphLink[] = []

  // 计算每列节点的垂直位置
  let chart_width:number = parseInt(getComputedStyle(document.querySelector('.chart') as HTMLElement).width) 
  let chart_height:number = parseInt(getComputedStyle(document.querySelector('.chart') as HTMLElement).height)
  let totalHeight:number = 550 * chart_height / chart_width
  let max_node_count:number =  Math.max(props.inboundProxies.length, props.outboundProxies.length, props.userGroups.length, props.routeSchemes.length)
 
  let spacing:number = totalHeight / (max_node_count - 1)
   console.log('max_node_count:', max_node_count)
  const calculateYPositions = (count: number) => {
    if (count <= 1) return [totalHeight / 2] // 单个节点居中
    const positions: number[] = [] 
    let node_range:number = spacing * (count - 1)
    let startY:number = totalHeight / 2 - node_range / 2
    for (let i = 0; i < count; i++) {
      positions.push(startY + spacing * i)
    }
    return positions
  }

  // 添加入站代理节点（第一列）
  const inboundPositions = calculateYPositions(props.inboundProxies.length)
  props.inboundProxies.forEach((proxy, index) => {
    nodes.push({
      id: `inbound-${proxy.id}`,
      name: proxy.id,
      symbolSize: 40,
      category: 0,
      value: proxy.enabled ? 1 : 0,
      itemStyle: {
        color: proxy.enabled ? '#4CAF50' : '#BDBDBD'
      },
      x: 100,
      y: inboundPositions[index],
      fixed: true
    })
  })

  // 添加用户组节点（第二列）
  const groupPositions = calculateYPositions(props.userGroups.length)
  props.userGroups.forEach((group, index) => {
    const groupNodeId = `group-${group.id}`
    nodes.push({
      id: groupNodeId,
      name: group.id,
      symbolSize: 40,
      category: 1,
      itemStyle: {
        color: '#2196F3'
      },
      x: 300,
      y: groupPositions[index],
      fixed: true
    })

    // 连接入站代理到用户组
    group.inboundProxyIds.forEach(proxyId => {
      links.push({
        source: `inbound-${proxyId}`,
        target: groupNodeId,
        value: 1
      })
    })
  })

  // 添加路由方案节点（第三列）
  const schemePositions = calculateYPositions(props.routeSchemes.length)
  props.routeSchemes.forEach((scheme, index) => {
    const schemeNodeId = `scheme-${scheme.id}`
    nodes.push({
      id: schemeNodeId,
      name: scheme.id,
      symbolSize: 40,
      category: 2,
      itemStyle: {
        color: scheme.enabled ? '#FF9800' : '#BDBDBD'
      },
      x: 500,
      y: schemePositions[index],
      fixed: true
    })

    // 从路由规则中获取出站代理
    scheme.rules?.forEach(rule => {
      rule.outbounds.forEach(proxyId => {
        links.push({
          source: schemeNodeId,
          info: "rule: " + rule.priority + " - " + rule.name,
          target: `outbound-${proxyId}`,
          value: 1
        })
      })
    })
  })

  // 添加出站代理节点（第四列）
  const outboundPositions = calculateYPositions(props.outboundProxies.length)
  props.outboundProxies.forEach((proxy, index) => {
    nodes.push({
      id: `outbound-${proxy.id}`,
      name: proxy.id,
      symbolSize: 40,
      category: 3,
      value: proxy.enabled ? 1 : 0,
      itemStyle: {
        color: '#9C27B0' ,
      },
      x: 700,
      y: outboundPositions[index],
      fixed: true
    })
  })

  // 连接用户组到路由方案
  props.userGroups.forEach(group => {
    const schemeNodeId = `scheme-${group.routeSchemeId}`
    links.push({
      source: `group-${group.id}`,
      target: schemeNodeId, 
      value: 1
    })
  })

  console.log('Generated nodes:', nodes)
  console.log('Generated links:', links)

  option.value.series[0].data = nodes
  option.value.series[0].links = links
}

const refreshGraph = () => {
  generateGraphData()
}

// 监听窗口大小变化
const handleResize = () => {
  console.log('Window resized, regenerating graph...')
  generateGraphData()
}

// 组件挂载时添加窗口大小变化监听
onMounted(() => {
  window.addEventListener('resize', handleResize)
  generateGraphData()
})

// 组件卸载时移除窗口大小变化监听
onUnmounted(() => {
  window.removeEventListener('resize', handleResize)
})
</script>

<style scoped>
.network-graph :deep(.el-card__header) {
  background-color: var(--el-color-primary-light-9);
}
 

h3 {
  margin: 0;
}

.chart {
  height: 60vh;
  width: 100%;
}
.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* 添加新的样式 */
:deep(.el-card) {
  height: 100%;
}

:deep(.el-card__header) {
  padding: 12px 20px;
  border-bottom: none;
}

</style> 