import { createRouter, createWebHistory } from 'vue-router'
import type { RouteRecordRaw } from 'vue-router'
import MainLayout from '../layouts/MainLayout.vue'
import Login from '../views/Login.vue'
import Dashboard from '../views/Dashboard.vue' 
import System from '../views/System.vue'
import UserProxies from '../views/UserProxies.vue'
import IntegratedManagement from '../views/IntegratedManagement.vue'
import { useUserStore } from '../stores/user' 

const routes: RouteRecordRaw[] = [
  {
    path: '/login',
    name: 'Login',
    component: Login,
    meta: { requiresAuth: false }
  },
  {
    path: '/',
    component: MainLayout,
    meta: { requiresAuth: true },
    children: [
      {
        path: '',
        name: 'Dashboard',
        component: Dashboard, 
        meta: { adminOnly: true }
      },
      {
        path: 'system',
        name: 'System',
        component: System, 
        meta: { adminOnly: true }
      },
      {
        path: 'my-proxies',
        name: 'UserProxies',
        component: UserProxies, 
        meta: { adminOnly: false }
      },
      {
        path: '/integrated',
        name: 'IntegratedManagement',
        component: IntegratedManagement, 
        meta: { adminOnly: true }
      }
    ]
  }
]

const router = createRouter({
  history: createWebHistory(),
  routes
})

// 路由守卫
router.beforeEach(async (to, from, next) => {
  const userGroupId = localStorage.getItem('userGroupId')
  console.log('userGroupId', userGroupId)
  console.log('to.meta.requiresAuth', to.path)
  // 如果路由没有匹配到，则跳转到登录页面
  if (to.matched.length<1){
    next('/login')
  }
  // 检查是否需要登录
  if (to.meta.requiresAuth) {
    if (userGroupId == null) {
      next('/login') 
    }else{
       if (to.meta.adminOnly) {
        if (userGroupId == "管理员") {
          next() 
        } else {
          next('/my-proxies') 
        }
      }else{
        next() 
      }
    }
  }else{
    if (to.path == '/login' && userGroupId != null){
      next('/')
    }else{
      next()
    }
  }
})

export default router